




























import TableAdvSearchBar from "@/common/components/search/TableAdvSearchBar.vue"
import TableSearchInputBar from "@/common/components/search/TableSearchInputBar.vue"

export default {
  name: "TableHeader",
  components: {TableAdvSearchBar, TableSearchInputBar},
  props: {
    title: String,
    showCreate: {
      type: Boolean,
      default: true
    },
    showRun: {
      type: Boolean,
      default: false
    },
    showThird: {
      type: Boolean,
      default: false
    },
    showSearch: {
      type: Boolean,
      default: true
    },
    createTip: {
      type: String,
      default: '新建'
    },
    runTip: {
      type: String,
    },
    thirdTip: {
      type: String,
    },
    iconCreate: {
      type: String,
      default: 'el-icon-circle-plus-outline'
    },
    iconRun: {
      type: String,
      default: 'el-icon-video-play'
    },
    iconThird: {
      type: String
    },
    isTesterPermission: {
      type: Boolean,
      default: false
    },
    placeholder: {
      String,
      default: '根据名称搜索'
    },
    condition: Object,
  },
  methods: {
    handleSearch(value: string) {
      this.$emit('search', value)
    },
    create() {
      this.$emit('create')
    },
    runTest() {
      this.$emit('runTest')
    },
    thirdClick() {
      this.$emit('thirdClick')
    }
  },
  computed: {
    isCombine() {
      return this.condition.components !== undefined && this.condition.components.length > 0
    }
  }
}
