












export default {
  name: "NodeBreadcrumb",
  data() {
    return {
      data: []
    }
  },
  props: {
    nodes: {
      type: Array
    },
    title: {
      type: String,
      default: '全部用例'
    }
  },
  watch: {
    nodes() {
      this.filterData()
    }
  },
  created() {
    this.filterData()
  },
  methods: {
    showAll() {
      this.$emit('refresh')
    },
    filterData() {
      this.data = this.nodes
      if (this.data.length > 4) {
        let lastData = this.data[this.data.length - 1]
        this.data.splice(1, this.data.length)
        this.data.push({id: lastData.id + '1', name: '...'})
        this.data.push(lastData)
      }
    }
  }
}
