























export default {
  name: "ShowMoreBtn",
  props: {
    isDisabled: {
      type: Boolean,
      default: true
    },
    buttons: Array,
    row: Object,
    size: Number
  },
  methods: {
    click(btn: any) {
      if (btn.handleClick instanceof Function) {
        btn.handleClick()
      }
    },
  }
}
