














export default {
  name: "TableSearchInputBar",
  props: {
    placeholder: {
      type: String,
      default: '根据名称搜索'
    }
  },
  data() {
    return {
      inputValue: '',
    }
  },
  methods: {
    handleSearch() {
      this.$emit("search", this.inputValue)
    },
    handleClear() {
      this.$refs.input.focus()
    }
  }
}
