















export default {
  name: "SelectMenu",
  props: {
    data: Array,
    title: String,
    currentData: Object,
  },
  data() {
    return {
      value: ''
    }
  },
  watch: {
    currentData() {
      this.value = this.currentData.id
    },
  },
  methods: {
    changeData(value: string) {
      const data = this.data.find((item: { id: string }) => item.id === value)
      this.$emit("dataChange", data)
    },
  },
}
